import { useEffect, useState } from 'preact/hooks'
import thankyou from '../../assets/thankyou/cdthankyou.svg'
import CDLayout from '../layout'
import style from './style.css'
import ErrorMessage from '../alert/errorMessage'

const CDThankYou = () => {
  const urlParams = new URLSearchParams(window.location.search)
  const leadId = urlParams.get('lid')
  const pathNames = typeof window !== 'undefined' && window.location.pathname
  const [message, setShowMessage] = useState() 
  useEffect(() => {
    if (typeof window !== 'undefined') {
      if (window.location.pathname !== '/result/') {
        window.scrollTo({ top: 0, behavior: 'smooth' });
      }
    }
  }, []);
  const unsubscribeLead = async (leadId) => {
    try {
      let responseData
      await fetch(`https://service.canadadistrict.com/forms/unsubscribeLead`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ leadId }),
      })
        .then((response) => response.json())
        .then((data) => (responseData = data))
      const { status } = responseData
      if (status === 200) {
      }
    } catch (err) {
      setShowMessage(
        err.response.data.msg ||
          err.response.data.message ||
          'Something went wrong',
      )
    }
  }
  useEffect(() => {
    if (leadId) {
      unsubscribeLead(leadId)
    }
  }, [leadId])
  const closeAction = () => {
    setShowMessage('')
  }
  return (
    
      <CDLayout>
        <div class={style.thankyou}>
          <div class={style.container}>
            <img src={thankyou} alt="thankyou" />
            <h2>Thank You!</h2>
            <p>
              {pathNames === '/thank-you/email_verification/' ||
              pathNames === '/thank-you/email_verification'
                ? `Your email is confirmed.`
                : pathNames === '/thank-you/unsubscribe/' ||
                  pathNames === '/thank-you/unsubscribe'
                ? `You were successfully removed from our mailing list.`
                : `An immigration professional will contact you shortly to complete
              your profile evaluation and discuss the next steps.`}
            </p>
          </div>
        </div>
        {message !== '' && message ? (
          <ErrorMessage message={message} closeAction={closeAction} />
        ) : null}
      </CDLayout>

  )
}

export default CDThankYou
